
// Grid
// ---------------------------

// Grid breakpoints

// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

$kite-grid-breakpoints: (
  xs: 0,
  sm: 480px,
  md: 600px,
  lg: 840px,
  xl: 960px,
  xxl: 1280px
) !default;

// Grid columns

// Set the number of columns and specify the width of the gutters.

$kite-grid-gutter-width-base: 1rem; // 16px
$kite-grid-gutter-width-lg:   1.25rem; // 20px
$kite-grid-row-margin-lg:     1.5rem; // 24px

$kite-grid-xs-columns: 4 !default;
$kite-grid-lg-columns: 12 !default;
