
@import '../kite-animation/variables';
@import '../kite-typography/variables';

// Buttons
// ---------------------------

$kite-btn-padding-x:                  20px !default;
$kite-btn-padding-y:                  12px !default;
$kite-btn-line-height:                1 !default;
$kite-btn-font-weight:                map-get($kite-font-weight-values, medium) !default;
$kite-btn-border-width:               $kite-border-width !default;
$kite-btn-focus-border-offset:        ($kite-focus-border-width * 2) + $kite-border-width !default;

$kite-btn-primary-border:             $kite-focus-border-color !default;
$kite-btn-primary-bg:                 $kite-blue-2 !default;
$kite-btn-primary-color:              $kite-inverse-color !default;

// $btn-secondary-border:           #ccc !default;
$kite-btn-secondary-bg:               $kite-gray !default;
$kite-btn-secondary-color:            $kite-dark-blue-1 !default;

// $btn-outline-border:          $inverse-color !default;
// $btn-outline-bg:              transparent !default;
// $btn-outline-color:           $inverse-color !default;

// $btn-info-border:                $btn-info-bg !default;
// $btn-info-bg:                    $brand-info !default;
// $btn-info-color:                 $white !default;

// $btn-success-border:             $btn-success-bg !default;
// $btn-success-bg:                 $brand-success !default;
// $btn-success-color:              $white !default;

// $btn-warning-border:             $btn-warning-bg !default;
// $btn-warning-bg:                 $brand-warning !default;
// $btn-warning-color:              $white !default;

// $btn-danger-border:              $btn-danger-bg !default;
// $btn-danger-bg:                  $brand-danger !default;
// $btn-danger-color:               $white !default;

$kite-btn-disabled-border:            transparent !default;
$kite-btn-disabled-bg:                #e8ebf0 !default;
$kite-btn-disabled-color:             #96afc1 !default;

$kite-btn-inverse-border:             $kite-inverse-color !default;

// $btn-link-disabled-color:        $gray-light !default;

// $btn-padding-x-sm:               .5rem !default;
// $btn-padding-y-sm:               .25rem !default;

// $btn-padding-x-lg:               1.5rem !default;
// $btn-padding-y-lg:               .75rem !default;

$kite-btn-block-spacing-y:            $kite-spacer-y !default;

// Allows for customizing button radius independently from global border radius
$kite-btn-border-radius:              $kite-border-radius !default;
$kite-btn-focus-radius:               $kite-border-radius !default;
// $btn-border-radius-lg:           $border-radius-lg !default;
// $btn-border-radius-sm:           $border-radius-sm !default;

$kite-btn-transition:                 background-color $kite-duration-fastest $kite-standard-curve, border-color $kite-duration-fastest $kite-standard-curve !default;
