
// Animation
// ---------------------------------------------------------

// https://material.io/guidelines/motion/duration-easing.html#duration-easing-common-durations

$kite-duration-fastest: 195ms;
$kite-duration-faster: 225ms;
$kite-duration-fast: 300ms;

// https://material.io/guidelines/motion/duration-easing.html#duration-easing-natural-easing-curves

$kite-standard-curve: cubic-bezier(0.4, 0, 0.2, 1); // within screen
$kite-deceleration-curve: cubic-bezier(0, 0, 0.2, 1); // ease-out, enter
$kite-acceleration-curve: cubic-bezier(0.4, 0, 1, 1); // ease-in, exit
$kite-sharp-curve: cubic-bezier(0.4, 0, 0.6, 1); // ease-in-out, re-enter anytime
