
@import '../kite-shared/variables';
@import '../kite-colors/variables';

// Paths
// ---------------------------

$kite-font-path: '/assets/fonts/' !default;

// Links
// ---------------------------

// Style anchor elements.

$kite-link-color:            $kite-brand-primary !default;
$kite-link-decoration:       underline !default;
$kite-link-hover-color:      $kite-brand-primary !default;
$kite-link-hover-decoration: underline !default;

// Typography
// ---------------------------

// Font, line-height, and color for body text, headings, and more.

$kite-font-family-sans-serif: Rutledge, -apple-system, system-ui, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif !default;
// $font-family-serif:      Georgia, "Times New Roman", Times, serif !default;
$kite-font-family-monospace:  Menlo, Monaco, "Andale Mono", "lucida console", "Courier New", monospace !default;
$kite-font-family-base:       $kite-font-family-sans-serif !default;

$kite-font-size-base: 1rem !default; // Assumes the browser default, typically `16px`
// $font-size-lg:   1.25rem !default;
// $font-size-sm:   .875rem !default;
// $font-size-xs:   .75rem !default;

$kite-font-weight-values: (
  //uberlight: 100,
  //extralight: 200,
  light: 200,
  normal: normal,
  medium: 500,
  //semibold: 600,
  bold: bold,
  //uberbold: 900
) !default;

//$font-weight-base:    $font-weight-normal !default;
$kite-font-weight-base:    map-get($kite-font-weight-values, normal) !default;
$kite-line-height-base:    1.5 !default;

$kite-headings-margin-bottom:  ($kite-spacer / 2) !default;
$kite-headings-font-family:    inherit !default;
$kite-headings-font-weight:    map-get($kite-font-weight-values, bold) !default;

$kite-headings-line-height:   1.1 !default;
$kite-headings-color:         inherit !default;

$kite-text-muted: $kite-gray !default;

$kite-blockquote-small-color:  $kite-gray-dark !default;
$kite-blockquote-font-size:    ($kite-font-size-base * 1.25) !default;
$kite-blockquote-border-color: $kite-brand-primary !default;
$kite-blockquote-border-width: 0.25rem !default;

$kite-hr-border-color: $kite-gray !default;
$kite-hr-border-width: $kite-border-width !default;

$kite-dt-font-weight: map-get($kite-font-weight-values, bold) !default;
