
@import '../kite-shared/mixins';
@import '../kite-elevation/mixins';
@import '../kite-theme/mixins';
@import './variables';

// Card
// ---------------------------------------------------------

.kite-card {
  background-color: $kite-card-bg;
  border-radius: $kite-card-border-radius;
  border: $kite-card-border-width solid $kite-card-border-color;
  padding: $kite-card-spacer-x;

  @include kite-media-breakpoint-up(lg) {
    padding: $kite-spacer-lg;
  }

  &.kite-card-active {
    //box-shadow: $elevation-shadow-8dp;
    box-shadow: kite-elevation(8);
  }
}

.kite-card-hoverable {
  @include kite-hover-focus {
    box-shadow: kite-elevation(2);
  }
}

// Themes
// ---------------------------

@include kite-theme('.kite-card') {
  background-color: $kite-inverse-bg;
  border-color: $kite-inverse-bg;
}
