
@import '../kite-shared/mixins';
@import '../kite-grid/variables';

// Scaffolding
// ---------------------------------------------------------

// Styles for common HTML elements and higher-level site-wide
// layout, like the header, main content and footer.

html {
  font-size: 16px;
}

body {
  height: auto;
}

// Layout containers
// ---------------------------

// Helper classes to enable making full-width
// sections, while locking "content" to a max-width.

.kite-container,
.kite-container-full .kite-content {
  margin-right: auto;
  margin-left: auto;
  max-width: kite-breakpoint-min(xxl);
  min-width: 320px;
  padding: 0 $kite-grid-gutter-width-base;

  @include kite-media-breakpoint-up(lg) {
    padding: 0 $kite-grid-row-margin-lg;
  }
}

.kite-container-full {
  max-width: none;
  padding: 0;
}
