
@import '../kite-shared/mixins';
@import '../kite-theme/mixins';
@import '../kite-form/variables';
@import './variables';

// Buttons
// ---------------------------------------------------------

.kite-btn {
  border: $kite-btn-border-width solid transparent;
  border-radius: $kite-btn-border-radius;
  cursor: pointer;
  display: block;
  font-weight: $kite-btn-font-weight;
  line-height: $kite-btn-line-height;
  margin-top: $kite-btn-block-spacing-y;
  max-width: 100%;
  padding: $kite-btn-padding-y $kite-btn-padding-x;
  position: relative;
  text-decoration: none;
  text-align: center;
  transition: $kite-btn-transition;
  white-space: nowrap;
  width: 100%;
  vertical-align: middle;
  user-select: none;

  &::before {
    border: $kite-focus-border-width solid transparent;
    border-radius: $kite-btn-focus-radius;
    content: '';
    position: absolute;
    top: -$kite-btn-focus-border-offset;
    right: -$kite-btn-focus-border-offset;
    bottom: -$kite-btn-focus-border-offset;
    left: -$kite-btn-focus-border-offset;
    transition: $kite-btn-transition;
  }

  // Share hover and focus styles
  @include kite-hover-focus {
    text-decoration: none;
  }

  &:focus {
    outline: 0;

    &::before {
      border-color: $kite-focus-border-color;
    }
  }

  // Disabled comes first so active can properly restyle
  &-disabled,
  &:disabled {
    cursor: $kite-cursor-disabled;
  }

  &-active,
  &:active {
    // background-image: none;
    // @include box-shadow($btn-focus-box-shadow, $btn-active-box-shadow);
  }

  @include kite-media-breakpoint-up(sm) {
    display: inline-block;
    margin-right: ($kite-spacer / 2);
    margin-left: ($kite-spacer / 2);
    width: auto;
  }
}

// Alternate buttons
// ---------------------------

.kite-btn-primary {
  background-color: $kite-btn-primary-bg;
  color: $kite-btn-primary-color;

  @include kite-hover-focus {
    background-color: $kite-focus-border-color;
    color: $kite-btn-primary-color; // override for `a.btn`
  }
}

.kite-btn-secondary {
  background-color: $kite-btn-secondary-bg;
  color: $kite-btn-secondary-color;

  @include kite-hover-focus {
    background-color: $kite-gray-dark;
    color: $kite-btn-primary-color;
    //outline-color: $kite-gray-dark;
  }

  &:focus {
    &::before {
      border-color: $kite-gray-dark;
    }
  }
}

.kite-btn-outline-primary {
  background-color: transparent;
  border-color: $kite-btn-primary-bg;
  color: $kite-btn-primary-bg;

  @include kite-hover-focus {
    background-color: $kite-focus-border-color;
    color: $kite-btn-primary-color;
  }
}

// Future-proof disabling of clicks on `<a>` elements
a.kite-btn.kite-btn-disabled,
fieldset[disabled] a.kite-btn {
  pointer-events: none;
}

// Specific disabled state
// ---------------------------

// Have to scope the disabled state to the alternate
// button class because of the different hover states.

.kite-btn-primary,
.kite-btn-secondary,
.kite-btn-outline-primary {
  &.kite-btn-disabled,
  &:disabled {
    background-color: $kite-btn-disabled-bg;
    border-color: $kite-btn-disabled-border;
    color: $kite-btn-disabled-color;
  }
}

// Link buttons
// ---------------------------

// Make a button look and behave like a link
.kite-btn-link {
  background-color: transparent;
  border-radius: 0;
  color: $kite-link-color;
  font-weight: map-get($kite-font-weight-values, normal);
  text-decoration: $kite-link-hover-decoration;

  @include kite-hover-focus {
    color: $kite-link-hover-color;
    text-decoration: $kite-link-hover-decoration;
  }

  // &:disabled {
  //   color: $btn-link-disabled-color;

  //   @include kite-hover-focus {
  //     text-decoration: none;
  //   }
  // }
}

.kite-btn-standalone-link,
.kite-btn-quick-link,
.kite-btn-control-link {
  text-decoration: none;

  @include kite-hover {
    text-decoration: underline;
  }
}

.kite-btn-quick-link {
  background:
    kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-link-color + '" d="M8 28l12-12L8 4l2-2 14 14-14 14"/></svg>')
    right $kite-btn-padding-x center / auto 1em
    no-repeat;
  padding-right: $kite-btn-padding-x * 2; // 2.5rem; `a.secondary-link` padding + `.btn` padding
}

.kite-btn-control-link {
  padding-right: 1.5rem;
  position: relative;

  &::after {
    background: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-link-color + '" d="M16 2C8.268 2 2 8.268 2 16s6.268 14 14 14 14-6.268 14-14S23.732 2 16 2zm-6 10l6 6 6-6 2 2-8 8-8-8 2-2z"/></svg>') no-repeat;
    content: '';
    overflow: hidden;
    position: absolute;
    top: 0;
    right: 0;
    transform: translateY(50%) rotate(0deg);
    transition: transform $kite-duration-fastest $kite-standard-curve;
    width: 1.2em;
    height: 1.2em;
  }

  &.kite-expanded {
    &::after {
      transform: translateY(50%) rotate(180deg);
    }
  }
}

// Button sizes
// ---------------------------

.kite-btn-sm {
  min-width: 90px;
}

.kite-btn-md {
  min-width: 130px;
}

.kite-btn-lg {
  min-width: 210px;
}

.kite-btn-xl {
  min-width: 288px;
}

// Block button
// ---------------------------

.kite-btn-block {
  display: block;
  margin-right: 0;
  margin-left: 0;
  width: 100%;
}

// Vertically space out multiple block buttons
.kite-btn-block + .kite-btn-block {
  margin-top: $kite-btn-block-spacing-y;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.kite-btn-block {
    width: 100%;
  }
}

// Themes
// ---------------------------

@include kite-theme('.kite-btn') {
  &:focus {
    &::before {
      border-color: $kite-inverse-color;
    }
  }
}

// Needs to be called out specifically because of
// `.theme-dark a` styles
@include kite-theme('a.kite-btn-primary') {
  &.kite-btn-disabled {
    color: $kite-btn-disabled-color;
  }
}

@include kite-theme('a.kite-btn-secondary') {
  color: $kite-btn-secondary-color;

  @include kite-hover-focus {
    color: $kite-btn-primary-color;
  }

  &.kite-btn-disabled {
    color: $kite-btn-disabled-color;
  }
}

@include kite-theme('.kite-btn-outline-primary') {
  border-color: $kite-btn-inverse-border;
  color: $kite-btn-inverse-border;

  @include kite-hover-focus {
    background-color: $kite-btn-inverse-border;
    color: $kite-dark-blue-2;
  }

  &.kite-btn-disabled,
  &:disabled {
    background-color: $kite-btn-disabled-bg;
    border-color: $kite-btn-disabled-border;
    color: $kite-btn-disabled-color;
  }
}

@include kite-theme('.kite-btn-link') {
  color: $kite-inverse-color;
}

@include kite-theme('.kite-btn-control-link') {
  &::after {
    background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M16 2C8.268 2 2 8.268 2 16s6.268 14 14 14 14-6.268 14-14S23.732 2 16 2zm-6 10l6 6 6-6 2 2-8 8-8-8 2-2z"/></svg>');
  }
}

@include kite-theme('.kite-btn-quick-link') {
  background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M8 28l12-12L8 4l2-2 14 14-14 14"/></svg>');
}

// Hide focus states
// ---------------------------

// Override the styles when `.kite-hide-focus` is present.

.kite-hide-focus {
  .kite-btn {
    &::before {
      visibility: hidden;
    }
  }

  .kite-btn-primary {
    &:focus {
      background-color: $kite-btn-primary-bg;

      @include kite-hover {
        background-color: $kite-focus-border-color;
      }
    }
  }

  .kite-btn-secondary {
    &:focus {
      background-color: $kite-btn-secondary-bg;
      color: $kite-btn-secondary-color;

      @include kite-hover {
        background-color: $kite-gray-dark;
        color: $kite-btn-primary-color;
      }
    }
  }

  @include kite-theme('a.kite-btn-secondary') {
    &:focus {
      color: $kite-btn-secondary-color;

      @include kite-hover {
        color: $kite-btn-primary-color;
      }
    }
  }

  .kite-btn-outline-primary {
    &:focus {
      background-color: transparent;
      color: $kite-btn-primary-bg;

      @include kite-hover {
        background-color: $kite-focus-border-color;
        color: $kite-btn-primary-color;
      }
    }
  }

  @include kite-theme('.kite-btn-outline-primary') {
    &:focus {
      color: $kite-btn-inverse-border;

      @include kite-hover {
        background-color: $kite-btn-inverse-border;
        color: $kite-dark-blue-2;
      }
    }
  }
}
