
@import '../kite-button/variables';

// Forms
// ---------------------------

$kite-input-padding-x:                 8px !default;
$kite-input-padding-y:                 11px !default;
$kite-input-line-height:               1 !default;

$kite-input-bg:                        $kite-white !default;
$kite-input-color:                     $kite-black !default;
$kite-input-border-color:              $kite-gray-dark !default;
$kite-input-border-width:              $kite-border-width !default; // For form controls and buttons
// $input-box-shadow:               inset 0 1px 1px rgba($black,.075) !default;
$kite-input-inverse-color:             $kite-inverse-color !default;

$kite-input-border-radius:             0 !default;
// $input-border-radius-lg:         $border-radius-lg !default;
// $input-border-radius-sm:         $border-radius-sm !default;

$kite-input-disabled-border:           transparent !default;
$kite-input-disabled-bg:               $kite-btn-disabled-bg !default;
$kite-input-disabled-color:            $kite-btn-disabled-color !default;

// $input-bg-focus:                 $input-bg !default;
$kite-input-border-focus:              $kite-blue-3 !default;
// $input-box-shadow-focus:         $input-box-shadow, rgba($input-border-focus, .6) !default;
$kite-input-color-focus:               $kite-input-color !default;
$kite-input-color-placeholder:         $kite-gray-3 !default;

$kite-input-transition:                background-color $kite-duration-fastest $kite-standard-curve, border-color $kite-duration-fastest $kite-standard-curve, box-shadow $kite-duration-fastest $kite-standard-curve !default;

$kite-form-text-margin-top:            0.25rem !default;
$kite-form-feedback-margin-top:        $kite-form-text-margin-top !default;

$kite-form-group-margin-bottom:        1.5rem !default;

$kite-cursor-disabled:                 not-allowed !default;

$kite-custom-control-gutter:                       2.25rem !default;
$kite-custom-control-spacer-x:                     0.5rem !default;
// $custom-control-spacer-y:                     .25rem !default;
$kite-custom-control-padding:                      0.25rem !default; // bigger tap area

$kite-custom-control-indicator-size:               1rem !default;
$kite-custom-control-indicator-bg:                 $kite-input-border-focus !default;
// $custom-control-indicator-bg-size:            50% 50% !default;
// $custom-control-indicator-box-shadow:         inset 0 .25rem .25rem rgba($black,.1) !default;
$kite-custom-control-border-width:                 2px !default;
$kite-custom-control-focus-border-width:           $kite-custom-control-border-width * 2 !default;

$kite-custom-control-disabled-cursor:              $kite-cursor-disabled !default;
$kite-custom-control-disabled-indicator-bg:        $kite-input-disabled-bg !default;
$kite-custom-control-disabled-description-color:   $kite-input-disabled-color !default;
$kite-custom-control-disabled-border:              $kite-custom-control-disabled-indicator-bg !default;

$kite-custom-checkbox-radius:                      $kite-border-radius-sm !default;

$kite-select-padding-y:                    12px !default;
$kite-select-indicator-padding:            34px !default; // Extra padding to account for the presence of the background-image based indicator
$kite-select-padding-right:                $kite-input-padding-x + $kite-select-indicator-padding !default;

// Define some variables to make dealing with focus state padding easier
$kite-input-focus-border-width:         $kite-focus-border-width;
$kite-input-focus-border-offset:        $kite-input-focus-border-width - $kite-input-border-width;
$kite-input-focus-padding-x:            $kite-input-padding-x - $kite-input-focus-border-offset;
$kite-input-focus-padding-y:            $kite-input-padding-y - $kite-input-focus-border-offset;
$kite-select-focus-padding-y:           $kite-select-padding-y - $kite-input-focus-border-offset;
$kite-select-focus-padding-right:       $kite-select-padding-right - $kite-input-focus-border-offset;
