
@import '../kite-shared/mixins';
@import './variables';
@import './mixins';

// Grid
// ---------------------------------------------------------

// Some rules here are applied using duplicate selectors.
// This is on purpose to increase their specificity when applied.
// For example: `.kite-cell-2-xs.kite-cell-2-xs`

.kite-grid {
  display: flex;
  flex-wrap: wrap;
  margin: 0 ($kite-grid-gutter-width-base / -2);

  @include kite-media-breakpoint-up(lg) {
    margin: 0 ($kite-grid-gutter-width-lg / -2);
  }

  > .kite-cell {
    padding: ($kite-grid-gutter-width-base / 2);

    @include kite-media-breakpoint-up(lg) {
      padding: ($kite-grid-gutter-width-lg / 2);
    }
  }

  &.kite-no-gutters {
    margin: 0;

    > .kite-cell {
      padding: 0;
    }
  }
}

// TODO(dura): Card content (like `.table-responsive`) could
// force cells to go wider than parent, assuming this is a
// bad thing, we give cells a `max-width: 100%;`.
.kite-cell {
  flex: 0 0 100%;
  max-width: 100%;

  > .kite-card {
    height: 100%; // Assume cards in a grid should be equal heights
  }
}

// Define partial sizes for available columns.
@for $i from 1 through ($kite-grid-xs-columns - 1) {
  @include kite-partial-size($i, 'xs', $kite-grid-xs-columns);
}
// Define 100% for columns above the max columns.
@for $i from $kite-grid-xs-columns through $kite-grid-lg-columns {
  @include kite-full-size($i, 'xs');
}
.kite-cell-flex { flex: 1; }

// gt-xs
@include kite-media-breakpoint-up(sm) {
  @for $i from 1 through ($kite-grid-xs-columns - 1) {
    @include kite-partial-size($i, 'gt-xs', $kite-grid-xs-columns);
  }

  @for $i from $kite-grid-xs-columns through $kite-grid-lg-columns {
    @include kite-full-size($i, 'gt-xs');
  }
  .kite-cell-flex-gt-xs.kite-cell-flex-gt-xs { flex: 1; }
}

// gt-sm
@include kite-media-breakpoint-up(md) {
  @for $i from 1 through ($kite-grid-xs-columns - 1) {
    @include kite-partial-size($i, 'gt-sm', $kite-grid-xs-columns);
  }

  @for $i from $kite-grid-xs-columns through $kite-grid-lg-columns {
    @include kite-full-size($i, 'gt-sm');
  }
  .kite-cell-flex-gt-sm.kite-cell-flex-gt-sm { flex: 1; }
}

// gt-md
@include kite-media-breakpoint-up(lg) {
  @for $i from 1 through $kite-grid-lg-columns {
    @include kite-partial-size($i, 'gt-md', $kite-grid-lg-columns);
  }
  .kite-cell-flex-gt-md.kite-cell-flex-gt-md { flex: 1; }
}

// gt-lg
@include kite-media-breakpoint-up(xl) {
  @for $i from 1 through $kite-grid-lg-columns {
    @include kite-partial-size($i, 'gt-lg', $kite-grid-lg-columns);
  }
  .kite-cell-flex-gt-lg.kite-cell-flex-gt-lg { flex: 1; }
}
