
/// Outputs a combination of rules that style a component based on a theme name.
///
/// @param {String} $selector - Any CSS selector
/// @param {String} $name 'theme-dark' - Name of the theme
///
/// @example scss - Basic Usage Sass
///   @include theme('.widget', 'theme-light') {
///     color: red;
///   }
///
/// @example css - Basic Usage CSS Output
///   .theme-light .widget,
///   .widget.theme-light  {
///     color: red;
///   }
///
/// @content This mixin allows extra content to be passed (through the `@content` directive).
@mixin kite-theme($selector, $name: 'kite-theme-dark') {
  .#{$name} #{$selector},
  #{$selector}.#{$name} {
    @content;
  }
}
