
@import '../kite-animation/variables';
@import '../kite-shared/mixins';
@import '../kite-theme/mixins';
@import './variables';

// Typography
// ---------------------------------------------------------

// Rutledge Typeface
// ---------------------------

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-light.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-light.woff') format('woff'),
    url($kite-font-path + 'rutledge-light.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, light);
  font-style: normal;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-lightitalic.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-lightitalic.woff') format('woff'),
    url($kite-font-path + 'rutledge-lightitalic.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, light);
  font-style: italic;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-regular.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-regular.woff') format('woff'),
    url($kite-font-path + 'rutledge-regular.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, normal);
  font-style: normal;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-regularitalic.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-regularitalic.woff') format('woff'),
    url($kite-font-path + 'rutledge-regularitalic.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, normal);
  font-style: italic;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-medium.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-medium.woff') format('woff'),
    url($kite-font-path + 'rutledge-medium.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, medium);
  font-style: normal;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-mediumitalic.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-mediumitalic.woff') format('woff'),
    url($kite-font-path + 'rutledge-mediumitalic.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, medium);
  font-style: italic;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-bold.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-bold.woff') format('woff'),
    url($kite-font-path + 'rutledge-bold.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, bold);
  font-style: normal;
}

@font-face {
  font-family: 'Rutledge';
  src:
    url($kite-font-path + 'rutledge-bolditalic.woff2') format('woff2'),
    url($kite-font-path + 'rutledge-bolditalic.woff') format('woff'),
    url($kite-font-path + 'rutledge-bolditalic.ttf') format('truetype');
  font-weight: map-get($kite-font-weight-values, bold);
  font-style: italic;
}

// Smoothing
// ---------------------------

// Normally, these would be applied to the `body` or `html` tags.
// However, scoping it to the `.kite-typography` class allows for more flexibility.

.kite-typography {
  font-family: $kite-font-family-base;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

// Headings
// ---------------------------

.kite-typography {
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  .kite-h1,
  .kite-h2,
  .kite-h3,
  .kite-h4,
  .kite-h5,
  .kite-h6 {
    color: $kite-headings-color;
    font-family: $kite-headings-font-family;
    font-weight: $kite-headings-font-weight;
    line-height: $kite-headings-line-height;
    letter-spacing: -1px;
    margin-bottom: $kite-headings-margin-bottom;
  }

  h1,
  .kite-h1 {
    font-size: 1.5rem; // 24px
    line-height: 1.29166667; // 31px
    margin-bottom: 1.5rem; // 24px
  }

  h2,
  .kite-h2 {
    font-size: 1.3125rem; // 21px
    line-height: 1.38095238; // 29px
    margin-bottom: 0.375rem; // 6px
  }

  h3,
  .kite-h3 {
    font-size: 1.125rem; // 18px
    line-height: 1.44444444; // 26px
    margin-bottom: 1rem; // 16px
  }

  h4,
  .kite-h4 {
    font-size: 1rem; // 16px
    letter-spacing: normal;
    line-height: $kite-line-height-base; // 24px
    margin-bottom: 0.5rem; // 8px
  }

  h5,
  .kite-h5 {
    font-size: 1rem; // 16px
    letter-spacing: normal;
    line-height: $kite-line-height-base; // 24px
    margin-bottom: 0.25rem; // 4px
  }

  h6,
  .kite-h6 {
    font-size: 1rem; // 16px
    letter-spacing: normal;
    line-height: $kite-line-height-base; // 24px
    margin-bottom: 0.25rem; // 4px
  }

  @include kite-media-breakpoint-up(md) {
    h1,
    .kite-h1 {
      font-size: 2.25rem; // 36px
      line-height: 1.25; // 45px
    }

    h2,
    .kite-h2 {
      font-size: 1.75rem; // 28px
      line-height: 1.28571429; // 36px
    }

    h3,
    .kite-h3 {
      font-size: 1.5rem; // 24px
      line-height: 1.29166667; // 31pt
    }

    h4,
    .kite-h4 {
      font-size: 1.25rem; // 20px
      letter-spacing: normal;
      line-height: 1.35; // 27px
    }

    h5,
    .kite-h5 {
      font-size: 1rem; // 16px
      letter-spacing: normal;
      line-height: $kite-line-height-base; // 24px
    }

    h6,
    .kite-h6 {
      font-size: 1rem; // 16px
      letter-spacing: normal;
      line-height: $kite-line-height-base; // 24px
    }
  }
}

// Articles
// ---------------------------

// A specialized typescale for troubleshooting articles
// and other article-ish pages with a lot of text content.
.kite-typography {
  article {
    h2,
    .kite-h2 {
      margin-bottom: 1rem; // 16px
    }
  }
}

// Body copy
// ---------------------------

.kite-typography {
  p {
    margin-bottom: 1.5rem;
  }
}


.kite-body-copy-sm {
  font-size: 0.875rem; // 14px
  margin-bottom: 1.25rem; // 20px
}

.kite-lead {
  font-size: 1.125rem; // 18px
}

// Type display classes
// ---------------------------

// Type display classes
.kite-display-1 {
  font-size: 1.75rem; // 28px
  font-weight: $kite-headings-font-weight;
  letter-spacing: -1px;
  line-height: 1.28571429; // 36px
  margin-bottom: 1rem;

  @include kite-media-breakpoint-up(md) {
    font-size: 2.25rem; // 36px
    line-height: 1.25; // 45px
  }
}

// Horizontal rules
// ---------------------------

.kite-typography {
  hr {
    margin-top: $kite-spacer-y;
    margin-bottom: $kite-spacer-y;
    border: 0;
    border-top: $kite-hr-border-width solid $kite-hr-border-color;
  }
}

// Emphasis
// ---------------------------

.kite-typography {
  mark,
  .kite-mark {
    background-color: $kite-gold;
  }
}


// Links
// ---------------------------

.kite-typography {
  a {
    font-weight: map-get($kite-font-weight-values, medium);

    &.kite-standalone-link,
    &.kite-quick-link,
    &.kite-control-link {
      text-decoration: none;

      @include kite-hover {
        text-decoration: underline;
      }
    }

    &.kite-quick-link {
      background: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-link-color + '" d="M8 28l12-12L8 4l2-2 14 14-14 14"/></svg>') center right / auto 90% no-repeat;
      padding-right: 1.25rem;
    }

    &.kite-control-link {
      padding-right: 1.5rem;
      position: relative;

      &::before {
        background: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-link-color + '" d="M16 2C8.268 2 2 8.268 2 16s6.268 14 14 14 14-6.268 14-14S23.732 2 16 2zm-6 10l6 6 6-6 2 2-8 8-8-8 2-2z"/></svg>') no-repeat;
        content: '';
        overflow: hidden;
        position: absolute;
        top: 0;
        right: 0;
        transform: rotate(0deg);
        transition: transform $kite-duration-fastest $kite-standard-curve;
        width: 1.2em;
        height: 1.2em;
      }

      &.kite-expanded {
        &::before {
          transform: rotate(180deg);
        }
      }
    }
  }
}

// Lists
// ---------------------------

.kite-typography {
  ol,
  ul {
    margin-bottom: 1.5rem;
    padding-left: 1.25rem;

    &.kite-list-unstyled {
      list-style: none;
      padding-left: 0;

      > li {
        padding-left: 0;
      }
    }

    &.kite-list-alpha {
      list-style: lower-alpha;
    }

    > li {
      margin-bottom: 0.75rem;
      padding-left: 0.5rem;
    }
  }
}

// Blockquotes
// ---------------------------

.kite-blockquote {
  padding: ($kite-spacer / 2) $kite-spacer;
  margin-bottom: $kite-spacer;
  font-size: $kite-blockquote-font-size;
  border-left: $kite-blockquote-border-width solid $kite-blockquote-border-color;
}

.kite-blockquote-footer {
  display: block;
  font-size: 80%; // back to default font-size
  color: $kite-blockquote-small-color;

  &::before {
    content: "\2014 \00A0"; // em dash, nbsp
  }
}

// Themes
// ---------------------------

// This catches the normal case where `.kite-typography` is on a container
// and `.kite-theme-dark` is applied further down the chain
.kite-typography {
  @include kite-theme('hr') {
    border-color: $kite-inverse-color;
  }

  @include kite-theme('a') {
    color: $kite-inverse-color;

    &.kite-quick-link {
      background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M8 28l12-12L8 4l2-2 14 14-14 14"/></svg>');
    }

    &.kite-control-link {
      &::before {
        background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M16 2C8.268 2 2 8.268 2 16s6.268 14 14 14 14-6.268 14-14S23.732 2 16 2zm-6 10l6 6 6-6 2 2-8 8-8-8 2-2z"/></svg>');
      }
    }
  }
}

// This is a little weird to catch a somewhat reasonable case
// where `.kite-typography` and `.kite-theme-dark` are
// both added to the same element, usually the `body` tag.
@include kite-theme('.kite-typography') {
  a {
    color: $kite-inverse-color;

    &.kite-quick-link {
      background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M8 28l12-12L8 4l2-2 14 14-14 14"/></svg>');
    }

    &.kite-control-link {
      &::before {
        background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-inverse-color + '" d="M16 2C8.268 2 2 8.268 2 16s6.268 14 14 14 14-6.268 14-14S23.732 2 16 2zm-6 10l6 6 6-6 2 2-8 8-8-8 2-2z"/></svg>');
      }
    }
  }

  hr {
    border-color: $kite-inverse-color;
  }
}
