
@mixin kite-partial-size($size, $bp, $columns) {
  .kite-cell-#{$size},
  .kite-cell-#{$size}-#{$bp}.kite-cell-#{$size}-#{$bp} {
    flex: 0 0 #{(($size / $columns) * 100) + '%'};
  }
}

@mixin kite-full-size($size, $bp) {
  .kite-cell-#{$size},
  .kite-cell-#{$size}-#{$bp} {
    flex: 0 0 100%;
  }
}
