
@import '../kite-shared/mixins';
@import './variables';

// Spectrum Icons
// ---------------------------------------------------------

// TODO(@dura): Use of icon fonts is not recommended.

// Since icons are fonts, they can be placed anywhere text is placed and are
// thus automatically sized to match the surrounding child. To use, create an
// inline element with the appropriate classes, like so:
// <a href="#"><span class="si si-rec-series"></span> Record Series</a>

@font-face {
  font-family: 'Spectrum Icons';
  src:
    url($kite-icon-font-path + 'sb-icons.woff') format('woff'),
    url($kite-icon-font-path + 'sb-icons.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

// Catch-all base class
.si {
  display: inline-block;
  font-family: 'Spectrum Icons' !important; // use `!important` to prevent issues with browser extensions that change fonts
  speak: none; // Not to be trusted, but hey.
}

@each $code, $name in $kite-icons {
  @if $name != '' {
    .si-#{$name} {
      &::before {
        content: kite-unicode($code); // Ugh... SASS has problems with escaping unicode
      }
    }
  }
}
