
@import '../kite-shared/mixins';
@import '../kite-theme/mixins';
@import './variables';

// Forms
// ---------------------------------------------------------

// Labels
// ---------------------------

label {
  color: $kite-input-color;
  font-weight: map-get($kite-font-weight-values, medium);
  line-height: 1;
}

// Textual form controls
// ---------------------------

.kite-form-control {
  background: right 0.5rem center / 1rem no-repeat border-box $kite-input-bg;
  border: $kite-input-border-width solid $kite-input-border-color;
  border-radius: $kite-input-border-radius;
  color: $kite-input-color;
  display: block;
  font-size: $kite-font-size-base;
  font-weight: map-get($kite-font-weight-values, medium);
  line-height: $kite-input-line-height;
  padding: $kite-input-padding-y $kite-input-padding-x;
  transition: $kite-input-transition;
  width: 100%;
  // Make inputs at least the height of their button counterpart (base line-height + padding + border)
  // height: $input-height;
  // Reset unusual Firefox-on-Android default style; see https://github.com/necolas/normalize.css/issues/214.
  background-image: none;
  background-clip: padding-box;

  // Note: This has no effect on <select>s in some browsers, due to the limited stylability of `<select>`s in CSS.
  // @if $enable-rounded {
  //   // Manually use the if/else instead of the mixin to account for iOS override
  //   border-radius: $input-border-radius;
  // } @else {
  //   // Otherwise undo the iOS default
  //   border-radius: 0;
  // }

  @include kite-hover-focus {
    border-color: $kite-input-border-focus;
  }

  &:focus {
    border-width: $kite-input-focus-border-width;
    color: $kite-input-color-focus;
    outline: none;
    padding: $kite-input-focus-padding-y $kite-input-focus-padding-x; // account for thicker focus border
  }

  // Unstyle the caret on `<select>`s in IE10+.
  &::-ms-expand {
    background-color: transparent;
    border: 0;
  }

  // Placeholder
  &::placeholder {
    color: $kite-input-color-placeholder;
    // Override Firefox's unusual default opacity; see https://github.com/twbs/bootstrap/pull/11526.
    opacity: 1;
  }

  // Disabled and read-only inputs
  //
  // HTML5 says that controls under a fieldset > legend:first-child won't be
  // disabled if the fieldset is disabled. Due to implementation difficulty, we
  // don't honor that edge case; we style them as disabled anyway.
  &:disabled,
  &[readonly] {
    background-color: $kite-input-disabled-bg;
    border-color: $kite-input-disabled-border;
    color: $kite-input-disabled-color;
    // iOS fix for unreadable disabled content; see https://github.com/twbs/bootstrap/issues/11655.
    opacity: 1;

    &::placeholder {
      color: $kite-input-disabled-color;
    }
  }

  &:disabled {
    cursor: $kite-cursor-disabled;
  }

  &.kite-form-control-danger {
    border-color: $kite-brand-danger;
  }

  &[type='password'] {
    background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-input-border-focus + '" d="M16.21 8A12.87 12.87 0 0 1 22 9.52a25.09 25.09 0 0 1 7 5.77l.35.43s-.14.17-.35.43a28.91 28.91 0 0 1-2.55 2.61c-2.78 2.53-6.46 4.67-10.31 4.67a12.7 12.7 0 0 1-5.82-1.53 21.63 21.63 0 0 1-4.5-3.15 26.78 26.78 0 0 1-2.55-2.59L3 15.72c.08-.11.18-.25.35-.45a25.28 25.28 0 0 1 7.06-5.75 14.33 14.33 0 0 1 2.78-1.09A10.87 10.87 0 0 1 16.21 8zm4.33 7.86a4.33 4.33 0 1 0-4.33 4.33 4.33 4.33 0 0 0 4.33-4.33zm-2.45 0A1.87 1.87 0 1 1 16.21 14a1.87 1.87 0 0 1 1.88 1.86z"/></svg>');
  }

  &.kite-password-confirmed {
    background-image:
      kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-brand-success + '" d="M26 6L12 20l-6-6-2 2 8 8L28 8"/></svg>'),
      kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-input-border-focus + '" d="M16.21 8A12.87 12.87 0 0 1 22 9.52a25.09 25.09 0 0 1 7 5.77l.35.43s-.14.17-.35.43a28.91 28.91 0 0 1-2.55 2.61c-2.78 2.53-6.46 4.67-10.31 4.67a12.7 12.7 0 0 1-5.82-1.53 21.63 21.63 0 0 1-4.5-3.15 26.78 26.78 0 0 1-2.55-2.59L3 15.72c.08-.11.18-.25.35-.45a25.28 25.28 0 0 1 7.06-5.75 14.33 14.33 0 0 1 2.78-1.09A10.87 10.87 0 0 1 16.21 8zm4.33 7.86a4.33 4.33 0 1 0-4.33 4.33 4.33 4.33 0 0 0 4.33-4.33zm-2.45 0A1.87 1.87 0 1 1 16.21 14a1.87 1.87 0 0 1 1.88 1.86z"/></svg>');
    background-position: right 1.5rem center, right 0.5rem center;
  }

  &.kite-password-show {
    background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-input-border-focus + '" d="M8.5 20.83a26.47 26.47 0 0 1-4.86-4.29L3 15.77l.11-.14.15-.17a25.61 25.61 0 0 1 5.06-4.63l3.8 3.8a4.25 4.25 0 0 0 0 2.57zm15.66 2a.63.63 0 0 1 .2.47.72.72 0 0 1-.67.69.67.67 0 0 1-.49-.2l-6.94-7-7 7a.64.64 0 0 1-.49.2.68.68 0 0 1-.67-.67.65.65 0 0 1 .2-.49l7-7-6.69-6.61a.64.64 0 0 1-.2-.47.68.68 0 0 1 .67-.67.65.65 0 0 1 .49.2L16.26 15 23 8.2a.64.64 0 0 1 .49-.2.69.69 0 0 1 .67.69.63.63 0 0 1-.2.47l-6.77 6.77zM15 11.77l-2.94-2.91a12 12 0 0 1 4.2-.86 12.29 12.29 0 0 1 4.2.81l-2.92 2.93a3.75 3.75 0 0 0-1.28-.2 4.29 4.29 0 0 0-1.26.23zm2.55 8.29l2.7 2.7a11.47 11.47 0 0 1-4 .73 11.73 11.73 0 0 1-4-.73L15 20.08a4.48 4.48 0 0 0 1.28.18 3.75 3.75 0 0 0 1.26-.19zm6.66-9.21a26.4 26.4 0 0 1 4.7 4.15l.61.73-.11.12a2.35 2.35 0 0 1-.15.18A25.12 25.12 0 0 1 24 20.82l-3.58-3.62a4.29 4.29 0 0 0 .18-1.27 4.49 4.49 0 0 0-.18-1.28z"/></svg>');

    &.kite-password-confirmed {
      background-image:
        kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-brand-success + '" d="M26 6L12 20l-6-6-2 2 8 8L28 8"/></svg>'),
        kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-input-border-focus + '" d="M8.5 20.83a26.47 26.47 0 0 1-4.86-4.29L3 15.77l.11-.14.15-.17a25.61 25.61 0 0 1 5.06-4.63l3.8 3.8a4.25 4.25 0 0 0 0 2.57zm15.66 2a.63.63 0 0 1 .2.47.72.72 0 0 1-.67.69.67.67 0 0 1-.49-.2l-6.94-7-7 7a.64.64 0 0 1-.49.2.68.68 0 0 1-.67-.67.65.65 0 0 1 .2-.49l7-7-6.69-6.61a.64.64 0 0 1-.2-.47.68.68 0 0 1 .67-.67.65.65 0 0 1 .49.2L16.26 15 23 8.2a.64.64 0 0 1 .49-.2.69.69 0 0 1 .67.69.63.63 0 0 1-.2.47l-6.77 6.77zM15 11.77l-2.94-2.91a12 12 0 0 1 4.2-.86 12.29 12.29 0 0 1 4.2.81l-2.92 2.93a3.75 3.75 0 0 0-1.28-.2 4.29 4.29 0 0 0-1.26.23zm2.55 8.29l2.7 2.7a11.47 11.47 0 0 1-4 .73 11.73 11.73 0 0 1-4-.73L15 20.08a4.48 4.48 0 0 0 1.28.18 3.75 3.75 0 0 0 1.26-.19zm6.66-9.21a26.4 26.4 0 0 1 4.7 4.15l.61.73-.11.12a2.35 2.35 0 0 1-.15.18A25.12 25.12 0 0 1 24 20.82l-3.58-3.62a4.29 4.29 0 0 0 .18-1.27 4.49 4.49 0 0 0-.18-1.28z"/></svg>');
    }
  }

  // Some date fields are taller, probably because the browser adds little control icons
  &[type='datetime-local'],
  &[type='date'],
  &[type='month'],
  &[type='week'],
  &[type='time'] {
    padding-top: $kite-input-padding-x;
    padding-bottom: $kite-input-padding-x;

    &:focus {
      padding-top: $kite-input-focus-padding-x;
      padding-bottom: $kite-input-focus-padding-x;
    }
  }
}

// Form groups
// ---------------------------

// Designed to help with the organization and spacing of vertical forms.

.kite-form-group {
  margin-bottom: $kite-form-group-margin-bottom;
}

.kite-form-text {
  display: block;
  margin-top: $kite-form-text-margin-top;
}

// Select
// ---------------------------

// Replaces the browser default select with a custom one, mostly pulled from
// http://primercss.io.

select.kite-form-control {
  // Use vendor prefixes as `appearance` isn't part of the CSS spec.
  -moz-appearance: none;
  -webkit-appearance: none;
  background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-gray-dark + '" d="M9 19l7 6.86L22.94 19 24 20l-8 8-8-8zm14-6l-7-6.86L9.06 13 8 12l8-8 8 8z"/></svg>');
  background-size: 2rem;
  padding: $kite-select-padding-y $kite-select-padding-right $kite-select-padding-y $kite-input-padding-x;

  &:focus {
    padding: $kite-select-focus-padding-y $kite-select-focus-padding-right $kite-select-focus-padding-y $kite-input-focus-padding-x;

    &::-ms-value {
      // For visual consistency with other platforms/browsers,
      // supress the default white text on blue background highlight given to
      // the selected option text when the (still closed) <select> receives focus
      // in IE and (under certain conditions) Edge.
      // See https://github.com/twbs/bootstrap/issues/19398.
      color: $kite-input-color;
      background-color: $kite-input-bg;
    }
  }

  // Hides the default caret in IE11
  &::-ms-expand {
    opacity: 0;
  }
}

// Checkboxes and radios
// ---------------------------

.kite-custom-control {
  display: block;
  line-height: $kite-line-height-base;
  padding: $kite-custom-control-padding; // bigger tap area
  padding-left: $kite-custom-control-gutter;
  position: relative;

  @include kite-media-breakpoint-up(lg) {
    display: inline-flex;
    margin-right: $kite-custom-control-spacer-x;
  }
}

.kite-custom-control-input {
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1; // Put the input behind the label so it doesn't overlay text
  width: 100%; // Makes for better SR outlines when focused
  height: 100%; // Makes for better SR outlines when focused

  &:checked {
    + .kite-custom-control-indicator {
      background-color: $kite-custom-control-indicator-bg;
      box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-border-focus;
    }
  }

  &:focus {
    + .kite-custom-control-indicator {
      box-shadow: 0 0 0 $kite-custom-control-focus-border-width $kite-focus-border-color;
    }
  }

  &:disabled,
  &:disabled:checked {
    + .kite-custom-control-indicator {
      cursor: $kite-custom-control-disabled-cursor;
      background-color: $kite-custom-control-disabled-indicator-bg;
      box-shadow: 0 0 0 $kite-custom-control-border-width $kite-custom-control-disabled-border;
    }

    ~ .kite-custom-control-description {
      color: $kite-custom-control-disabled-description-color;
      cursor: $kite-custom-control-disabled-cursor;
    }
  }
}

.kite-custom-control-indicator {
  display: block;
  position: absolute;
  top: (($kite-line-height-base - $kite-custom-control-indicator-size) / 2) + 0.25rem; // + padding for hit area
  left: 0;
  border-radius: $kite-custom-checkbox-radius / 2; // because outer box-shadow makes radius more extreme
  box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-border-color;
  cursor: pointer;
  transition: $kite-input-transition;
  user-select: none;
  width: $kite-custom-control-indicator-size;
  height: $kite-custom-control-indicator-size;
}

// Checkboxes
.kite-custom-checkbox {
  .kite-custom-control-input {
    &:checked {
      + .kite-custom-control-indicator {
        background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-input-inverse-color + '" d="M26 6L12 20l-6-6-2 2 8 8L28 8"/></svg>');
        background-repeat: no-repeat;
        background-size: contain;
      }

      &:focus {
        + .kite-custom-control-indicator {
          box-shadow: 0 0 0 0 transparent;

          &::after {
            $custom-control-focus-offset: -(0.125rem + (0.125rem * 2)); // focus border width + outline offset
            $custom-control-focus-size: $kite-custom-control-indicator-size + (0.125rem * 2) + (0.125rem * 2); // indicator-size + border-width + offset
            $custom-control-focus-offset: -0.25rem;

            background-color: transparent;
            border: $kite-focus-border-width solid $kite-input-border-focus;
            border-radius: $kite-custom-checkbox-radius * 2;
            content: '';
            display: block;
            position: absolute;
            top: $custom-control-focus-offset;
            left: $custom-control-focus-offset;
            transition: $kite-input-transition;
            width: $custom-control-focus-size;
            height: $custom-control-focus-size;
          }
        }
      }
    }

    &:checked:disabled {
      + .kite-custom-control-indicator {
        background-image: kite-svg-url('<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 32 32"><path fill="' + $kite-custom-control-disabled-description-color + '" d="M26 6L12 20l-6-6-2 2 8 8L28 8"/></svg>');
      }
    }
  }
}

// Radios
.kite-custom-radio {
  .kite-custom-control-input {
    &:checked {
      + .kite-custom-control-indicator {
        background-color: transparent;

        &::before {
          background-color: $kite-input-border-focus;
        }
      }
    }

    &:checked:disabled {
      + .kite-custom-control-indicator {
        background-color: $kite-custom-control-disabled-indicator-bg;

        &::before {
          background-color: $kite-custom-control-disabled-description-color;
        }
      }
    }
  }

  .kite-custom-control-indicator {
    border-radius: 50%;

    &::before {
      background-clip: content-box;
      background-color: transparent;
      border-radius: 50%;
      content: '';
      padding: 0.25rem;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    &::after {
      border-radius: 50%;
    }
  }
}

// Form control feedback states
// ---------------------------

// Apply contextual and semantic states to individual form controls.

.kite-form-control-feedback {
  line-height: 1.2;
  margin-top: $kite-form-feedback-margin-top;
  color: $kite-brand-danger; // Assume the only feedback is DANGER!
}

// Themes
// ---------------------------

@include kite-theme('label') {
  color: $kite-input-inverse-color;
}

@include kite-theme('.kite-form-control') {
  @include kite-hover-focus {
    border-color: $kite-brand-primary-theme-dark;
  }

  &:disabled,
  &[readonly] {
    border-color: $kite-input-disabled-border; // override hover state
  }

  &.kite-form-control-danger {
    border-color: $kite-red;
  }
}

@include kite-theme('.kite-form-control-feedback') {
  color: $kite-red; // Assumes the only feedback is DANGER!
}

@include kite-theme('.kite-custom-control-input') {
  &:checked {
    + .kite-custom-control-indicator {
      background-color: $kite-brand-primary-theme-dark;
      box-shadow: 0 0 0 $kite-custom-control-border-width $kite-brand-primary-theme-dark;
    }
  }

  &:focus {
    + .kite-custom-control-indicator {
      box-shadow: 0 0 0 $kite-custom-control-focus-border-width $kite-input-inverse-color;
    }
  }

  &:disabled,
  &:disabled:checked {
    + .kite-custom-control-indicator {
      background-color: $kite-custom-control-disabled-indicator-bg;
      box-shadow: 0 0 0 $kite-custom-control-border-width $kite-custom-control-disabled-border;
    }
  }
}

@include kite-theme('.kite-custom-control-indicator') {
  box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-inverse-color;
}


@include kite-theme('.kite-custom-checkbox') {
  .kite-custom-control-input {
    &:checked {
      &:focus {
        + .kite-custom-control-indicator {
          &::after {
            border-color: $kite-brand-primary-theme-dark;
          }
        }
      }
    }
  }
}

@include kite-theme('.kite-custom-radio') {
  .kite-custom-control-input {
    &:checked {
      + .kite-custom-control-indicator {
        background-color: transparent;

        &::before {
          background-color: $kite-brand-primary-theme-dark;
        }
      }
    }

    &:checked:disabled {
      + .kite-custom-control-indicator {
        background-color: $kite-custom-control-disabled-indicator-bg;

        &::before {
          background-color: $kite-custom-control-disabled-description-color;
        }
      }
    }
  }
}

// Hide focus states
// ---------------------------

.kite-hide-focus {
  .kite-custom-control-input {
    &:focus {
      + .kite-custom-control-indicator {
        box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-border-color;
      }
    }
  }

  @include kite-theme('.kite-custom-control-input') {
    &:focus {
      + .kite-custom-control-indicator {
        box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-inverse-color;
      }
    }
  }

  .kite-custom-checkbox {
    .kite-custom-control-input {
      &:checked {
        &:focus {
          + .kite-custom-control-indicator {
            box-shadow: 0 0 0 $kite-custom-control-border-width $kite-input-border-focus;

            &::after {
              display: none;
            }
          }
        }
      }
    }
  }

  @include kite-theme('.kite-custom-checkbox') {
    .kite-custom-control-input {
      &:checked {
        &:focus {
          + .kite-custom-control-indicator {
            box-shadow: 0 0 0 $kite-custom-control-border-width $kite-brand-primary-theme-dark;
          }
        }
      }
    }
  }
}
